html {

  box-sizing: border-box;
}
body {
  font-family: var(--font-family, sans-serif);
  font-size: 16px;
  font-weight: 400;
}
* {
  padding: 0;
  margin: 0;
  border: 0;
}


*,
*::before,
*::after {
 -webkit-box-sizing: border-box;
  box-sizing: border-box;

}
:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}
a,
a:visited {
  text-decoration: none;

}
a:hover {
  text-decoration: none;
}

nav,
footer,
header,
aside {
  display: block;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку

}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  position: relative;

  @include small-desktop {
    padding: 0 var(--container-offset-mobile);
    max-width: var(--container-width-mobile);
  }

}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}
