.service {
  margin-bottom: var(--mb-block);
  @include tablet {
    margin-bottom: var(--mb-block-tablet);
  }
}

.service__block {
  @include flex-all-sb;
  align-items: stretch;
  @include tablet {
    margin: 0 auto;
    max-width: 600px;
    justify-content: center;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
.service__list {
  display: flex;
  flex-direction: column;
  flex-basis: content;
  border: 1px solid #E0E0E0;
  padding: 22px 16px;
   &:nth-last-child(n+3) {
   @include tablet {
    margin: 0 0 45px 0;
   }
}
}
.sub__block {
  @include flex-v-center;
  padding: 0 0 16px 0;
}
.sub-svg {
  width: 48px;
  height: 48px;
  stroke: #156963;
  fill: none;
}
.subtitle {
  margin-left: 16px;
  max-width: 137px;
  font-family: var(--font-family, sans-serif);
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: var(--color-main);
}
.sub_text {
  max-width: 236px;
  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  font-size: 10px;
  line-height: 16px;
  color: var(--color-text);
}
