.relaxed {
  margin-bottom: var(--mb-block);
  @include tablet {
    margin-bottom: var(--mb-block-tablet);
  }
}
.relaxed_block {
  display: flex;
  justify-content: space-between;

}
.relaxed_block__title {
   max-width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
   margin: 0 25px 0 0;
  @include tablet {
    max-width: 530px;


  }
}

.subtitle-h2 {
    font-family: var(--font-family, sans-serif);
  font-weight: 700;
  font-size: 40px;
  line-height: 56px;
  word-break: normal;

}
.relaxed_text {
   margin: 16px 0 40px 0;
}


.relaxed__img {
  max-width: 50%;
   @include small-desktop {
    max-width: 44%;
  }
}
