.main {
  margin-bottom: var( --mb-block-main);
  @include tablet {
    margin-bottom: var(--mb-block-tablet);
  }
}

.main__item {
  display: flex;
  justify-content: space-between;
  // outline: hidden;
  min-height: 740px;
  @include small-desktop {
    min-height: 620px;
  }
  @include tablet {
    min-height: 534px;
  }
  @include light-tablet {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 950px;
  }
}

.main__Slider {
	height: 740px;
  @include light-tablet {
    height: 950px;
  }

}
.main__Slider.slick-initialized {
	height: auto;
}

.main__photo {
  width: 47.5%;
  min-height: 740px;
  @include small-desktop {
    width: 38.5%;
    min-height: 620px;
  }
  @include tablet {
    min-height: 534px;
  }
   @include light-tablet {
    width: 50%;
   order: 1;
   display: flex;
   justify-content: center;

  }
}

.main__img {
  height: 100%;
 }

.main__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 42%;

  @include for-desktop {
    width: 50%;
    }
  @include small-desktop {
    width: 58%;
    }
  @include tablet {
    width: 58%;
  }
   @include light-tablet {
   margin: 120px 0 25px 0;
   width: 100%;
   order: 0;
   align-items: center;
  }

}

.main__title {
  font-family: var(--font-family, sans-serif);
  font-weight: 500;
  font-size: 68px;
  line-height: 83px;
  color: var(--color-main);
  margin: 0 0 24px 0;
   @include light-tablet {
  text-align: center;
  font-size: 48px;
  line-height: 58px;
  }
}

.main__text {
   font-family: var(--font-family, sans-serif);
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  color: var(--color-text);
  max-width: 493px;
   @include light-tablet {
  text-align: center;
  font-size: 14px;
  line-height: 24px;
  }
}

