/* stylelint-disable */

@mixin for-desktop {
  @media (max-width: (1368px)) {
    @content;
  }
}

@mixin small-desktop {
  @media (max-width: (1280px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}
@mixin light-tablet {
  @media (max-width: (950px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
