.footer {
  margin-bottom: 25px;
}
.nav_item {
  display: flex;
 justify-content: space-evenly;
}
.footer_list {
  display: flex;
  flex-direction: column;
}
.footer_logo{
  font-family: var(--font-family, sans-serif);
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  color: var(--color-main);
  text-transform: uppercase;
  margin-bottom: 24px;
}
.footer_subtitle {
  font-family: var(--font-family, sans-serif);
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  color: #000000;
  margin-bottom: 14px;
}
.footer_address {
  max-width: 286px;

  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  font-size: 13px;
  line-height: 20px;
}

.footer_link {
  margin-bottom: 14px;
}

.footer__btn {
  width: 51px;
  height: 22px;
  margin-top: 14px;
  padding: 4px 12px;
  border-radius: 2px;
 font-family: var(--font-family, sans-serif);
  font-weight: 400;
  font-size: 8px;
  color: white;
  background: var(--color-main);

}
.form {
  display: flex;
  flex-direction: column;
}

.form__input {
  max-width: 100%;
  border: 1px solid #F2F2F2;
  padding: 8px 0 8px 12px;
  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  font-size: 12px;
}
