.header {
  width: 100%;
  height: 36px;
  position: absolute;
  top: 40px;
  left: 0;
  z-index: 10;

}

.nav__item {
  &--btn {
  @include  flex-all-sb;
  }
  &--logo {
    margin-left: 60px;
  }
}

.nav__list {
  @include  flex-all-sb;
}
.logo {
  width: 36px;
  height: 36px;

}
.header__icon{
  width: 20px;
  height: 20px;
  stroke: #333333;
  fill: none;
}

.search {
  display: none;
}
.search__btn {
}
.header__icon {
}
.icon__btn {
  width: 20px;
  height: 20px;
&--shopping-cart {
  margin: 0 8px 0 80px;
}
}
.btn {
   width: 111px;
    height: 36px;
   font-family: var(--font-family, sans-serif);
   font-weight: 700;
    font-size: 14px;
    background-color: white;
  color: var(--color-main);
  @include flex-all-center;
  cursor: pointer;

  &:hover,&:focus,&:active {
  background-color:  var(--color-main);
  color: white;
  }

}


