
.main-slider {
	@include flex-h-center;
	position: absolute;
	bottom: 0;
	right: var(--container-offset);
   @include for-desktop {
    	right: var(--container-offset-mobile);
    }
}

.btn-slider {
  @include flex-h-center;
  flex-direction: column;
  max-width: 320px;
  max-height: 160px;
  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  font-size: 32px;
  background-color: white;
  color: var(--color-main);
  cursor: pointer;
  padding: 36px 50px;
   &:hover,&:focus,&:active {
  background-color:  var(--color-main);
  color: white;
  .arrow {
    color: #F2F2F2;
  }
  }
   @include for-desktop {
    padding: 35px 40px;
    }
  @include tablet {
    padding: 25px 30px;
    font-size: 20px;
    }

}
.arrow {
  padding: 12px 0;
  font-size: 24px;
  color: #333333;
 @include small-desktop {
    font-size: 15px;
    }
}


