.visit-shop {
  margin-bottom: var(--mb-block);
  @include tablet {
    margin-bottom: var(--mb-block-tablet);
  }
}

.visit-shop__block {
  background: #F3F4F6;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.visit-shop__block_title {
  margin-left: 95px;

}
.visit-shop__title {
  margin-bottom: 32px;
  max-width: 585px;
  font-family: var(--font-family, sans-serif);
  font-weight: 700;
  font-size: 40px;
  line-height: 56px;
  word-break: normal;

}
.visit-shop__btn {
  background: var(--color-main);
  color: white;
  width: 152px;
}

.visit-shop__img {
}

