.featured {
  margin-bottom: var(--mb-block);
  @include tablet {
    margin-bottom: var(--mb-block-tablet);
  }
}

.featured__block-title {
   display: flex;
  justify-content: space-between;
  padding-bottom: 16px;
  border-bottom: 1px solid #E0E0E0;

}
.featured__title {
  font-family: var(--font-family, sans-serif);
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: var(--color-text);

}
.featured__filter {
  font-family: var(--font-family, sans-serif);
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: var(--color-text);
  cursor: pointer;
   padding: 8px 12px;
  border: 1px solid #E0E0E0;

}
.filter__option {
   padding: 8px 12px;
  border: 1px solid #E0E0E0;
  &:hover,&:focus {
    border: 2px solid #E0E0E0;
  }

}

.featured__block-plants {
  margin-top: 24px;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: space-between;
  align-content: flex-start;
  @include tablet {
    justify-content: flex-start;
  }

}
.fearured__plants {
  max-width: 217px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  flex-basis: content;
  margin-bottom: 40px;
  &:not(:last-child) {
    margin-right: 10px;
  }

}
.img__plants {

  max-height: 217px;
  background: #F3F4F6;
  margin-bottom: 16px;
}

.name__plants {
    font-family: var(--font-family, sans-serif);
  font-weight: 400;
  font-size: 16px;
  margin-bottom: 6px;
}
.price__plants {
    font-family: var(--font-family, sans-serif);
  font-weight: 700;
  font-size: 16px;
}
